import { BrowserRouter, Routes, Route } from "react-router-dom";
import React from 'react';
import ReactDOM from 'react-dom';
import { Play } from "./pages/Play";
import { Home } from "./pages/Home";
import { Listen } from "./pages/Listen";
import { Serve } from "./pages/Serve";
import Edit from "./pages/Edit";

import '@fontsource/roboto/300.css';
import '@fontsource/roboto/400.css';
import '@fontsource/roboto/500.css';
import '@fontsource/roboto/700.css';

import { DBConfig } from "./DBConfig";
import { initDB } from "react-indexed-db-hook";

initDB(DBConfig);


ReactDOM.render(
  <BrowserRouter>
    <Routes>
      <Route path={`${process.env.PUBLIC_URL}`} element={<Edit />} />
      <Route path={`${process.env.PUBLIC_URL}/edit`} element={<Edit />} />
      <Route path={`${process.env.PUBLIC_URL}/home`} element={<Home />} />
      <Route path={`${process.env.PUBLIC_URL}/play`} element={<Play />} />
      <Route path={`${process.env.PUBLIC_URL} /serve`} element={<Serve />} />
      <Route path={`${process.env.PUBLIC_URL}/listen`} element={<Listen />} />
    </Routes>
  </BrowserRouter>,
  document.getElementById('root')
);

