
import Editor from "../../components/Editor";
import FileUploader from "../../components/FileUploader";
import { Track } from "../../components/Editor/types";
import { AppBar, Box, CssBaseline, Toolbar, Typography, Link } from "@mui/material/";
import { ThemeProvider } from '@mui/material/styles';
import theme from '../../theme';
import { useState, useCallback, useEffect } from 'react';
import { FileBundle, NamedUrl, loadFiles } from '../../midi/load';
import { useSearchParams } from "react-router-dom";
import { useIndexedDB } from 'react-indexed-db-hook';
import dayjs from "dayjs";

const Page = () => {
  const [searchParams] = useSearchParams();

  const [midiUrl, setMidiUrl] = useState<NamedUrl | undefined>();
  const [audioUrl, setAudioUrl] = useState<NamedUrl | undefined>();
  const [midizUrl, setMidizUrl] = useState<string>("");
  const [shouldLoadFiles, setShouldLoadFiles] = useState(false);
  const [tracks, setTracks] = useState<Track[]>([]);
  const [fileBundle, setFileBundle] = useState<FileBundle | null>(null);
  const [searchForSavedFiles, setSearchForSavedFiles] = useState(false);

  const loadFileBundle = useCallback(async (f: FileBundle) => {
    const tracks = await loadFiles(f, { verbose: true });

    setTracks(tracks);
    setFileBundle(f);
  }, [setTracks]);

  const files = useIndexedDB("file");
  const ws = useIndexedDB("workspace");

  // TODO: Fix file caching.
  useEffect(() => {
    if (!files) return;
    if (!ws) return;
    const find = async () => {
      const findInDB = async () => {
        const workspaces = await ws.getAll();
        if (!workspaces.length) return;

        const workspacesSortedByDate = workspaces.sort((a, b) => dayjs(b.date).diff(dayjs(a.date)));
        const latestWorkspace = workspacesSortedByDate[0];
        const [audioFile, midiFile, midizFile] = await Promise.all([
          files.getByID(latestWorkspace.audio_id),
          files.getByID(latestWorkspace.midi_id),
          files.getByID(latestWorkspace.midiz_id),
        ]);

        console.log("found", { audioFile, midiFile, midizFile });

        if (!audioFile) return;
        if (!midiFile) return;

        const audioBlob = audioFile.blob;
        const audioBlobUrl = URL.createObjectURL(audioBlob);
        const audioUrl = { url: audioBlobUrl, name: audioFile.name };
        setAudioUrl(audioUrl);

        const midiBlob = midiFile.blob;
        const midiBlobUrl = URL.createObjectURL(midiBlob);
        const midiUrl = { url: midiBlobUrl, name: midiFile.name };
        setMidiUrl(midiUrl);
      }
      try {
        await findInDB();
      } catch (e: any) {
        console.error("Error finding files in db", e);
      }
      setSearchForSavedFiles(true);
    }
    find();
  }, [files, ws]);

  useEffect(() => {
    if (!searchParams) return;

    if (searchParams.has("test")) {
      setMidiUrl({
        url: `${process.env.PUBLIC_URL}/assets/nogo.mid`,
        name: "/assets/nogo.mid",
      });
      setAudioUrl({
        url: `${process.env.PUBLIC_URL}/assets/nogo.mp3`,
        name: "/assets/nogo.mp3",
      });
      setMidizUrl(`${process.env.PUBLIC_URL}/assets/nogo.mdz`);
      setShouldLoadFiles(true);
      return;
    }

    if (searchParams.has("autoload")) {
      setShouldLoadFiles(true);
    }
  }, [setMidiUrl, setAudioUrl, setMidizUrl, searchParams]);

  useEffect(() => {
    if (!shouldLoadFiles) return;
    if (!midiUrl) return;
    if (!audioUrl) return;
    const load = async () => {
      const b = {
        midi: midiUrl,
        audio: audioUrl,
        midizUrl,
      };
      await loadFileBundle(b);
    }
    load();
  }, [shouldLoadFiles, loadFileBundle, midiUrl, audioUrl, midizUrl]);

  const useHeader = false;
  const Header = () => {
    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const menuOpen = Boolean(anchorEl);

    return <>
      <AppBar position="fixed">
        <Toolbar variant="dense">
        </Toolbar>
      </AppBar>
    </>
      ;
  }

  return <>
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {useHeader ? <Header /> : undefined}
      {searchForSavedFiles &&
        <Box sx={{ mt: useHeader ? 6 : undefined, width: "100%", p: 1 }} >
          {fileBundle
            ? <Editor
              midiUrl={fileBundle.midi}
              audioUrl={fileBundle.audio}
              tracks={tracks}
            />
            : <>
              <Typography variant="body1">Upload an audio file and a MIDI file to get started.</Typography>
              <FileUploader
                midiUrl={midiUrl}
                audioUrl={audioUrl}
                setMidiUrl={setMidiUrl}
                setAudioUrl={setAudioUrl}
                setShouldLoadFiles={setShouldLoadFiles}
              />
              <Box sx={{ mt: 2 }}>
                <Typography variant="body1">
                  You can also bypass this annoying step and go <Link href={`${process.env.PUBLIC_URL}/edit?test`}>test</Link> for test.
                </Typography>
              </Box>
            </>
          }
        </Box>
      }
    </ThemeProvider >
  </>;
}

export default Page;