
import { Box, Button, Typography, ButtonGroup } from "@mui/material";
import { useState, useMemo, useCallback, useEffect } from "react";
import { NamedUrl } from "../midi/load";
import { useIndexedDB } from "react-indexed-db-hook";
import dayjs from "dayjs";

interface FileUploaderProps {
  midiUrl?: NamedUrl;
  audioUrl?: NamedUrl;
  setMidiUrl: React.Dispatch<React.SetStateAction<NamedUrl>>;
  setAudioUrl: React.Dispatch<React.SetStateAction<NamedUrl>>;
  setShouldLoadFiles: React.Dispatch<React.SetStateAction<boolean>>;
}

const FileUploader: React.FC<FileUploaderProps> = ({
  midiUrl: pageMidiUrl,
  audioUrl: pageAudioUrl,
  setMidiUrl: pageSetMidiUrl,
  setAudioUrl: pageSetAudioUrl,
  setShouldLoadFiles,
}) => {
  const files = useIndexedDB("file");
  const ws = useIndexedDB("workspace");

  const [audioUrl, setAudioUrl] = useState<NamedUrl | undefined>(pageAudioUrl);
  const [midiUrl, setMidiUrl] = useState<NamedUrl | undefined>(pageMidiUrl);

  console.log("audioUrl", audioUrl);
  console.log("pageAudioUrl", pageAudioUrl);

  const handleAudioFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      const f = event.target.files[0];
      setAudioUrl({ url: "", name: f.name });
      const url = await upload(f);
      setAudioUrl({ url, name: f.name });
    }
  };

  const handleMidiFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      const f = event.target.files[0];
      setMidiUrl({ url: "", name: f.name });
      const url = await upload(f);
      setMidiUrl({ url, name: f.name });
    }
  };

  const upload = async (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        const blobUrl = URL.createObjectURL(file);
        resolve(blobUrl);
      };
      reader.onerror = () => {
        reject(new Error("Failed to read file"));
      };
      reader.readAsArrayBuffer(file);
    });
  };

  const saveToDB = async (audioUrl: NamedUrl, midiUrl: NamedUrl) => {
    const workspace = await ws.add({
      date: dayjs().toISOString(),
    });

    const [audioFile, midiFile, mdzFile] = await Promise.all([
      async function () {
        const audioBlob = await (await fetch(audioUrl.url)).blob();
        const audioFile = await files.add({
          workspace_id: workspace,
          name: audioUrl.name,
          type: "audio",
          blob: audioBlob,
        });
        return audioFile;
      }(),
      async function () {
        const midiBlob = await (await fetch(midiUrl.url)).blob();
        const midiFile = await files.add({
          workspace_id: workspace,
          name: midiUrl.name,
          type: "midi",
          blob: midiBlob,
        });
        return midiFile;
      }(),
      async function () {
        const midizBlob = new Blob([""], { type: "application/json" });
        const name = midiUrl.name.replace(/\.mid$/, ".mdz");
        const mdzFile = await files.add({
          workspace_id: workspace,
          name,
          type: "midiz",
          blob: midizBlob,
        });
        return mdzFile;
      }(),
    ]);
    const update = await ws.update({
      id: workspace,
      audio_id: audioFile,
      midi_id: midiFile,
      midiz_id: mdzFile,
    });
    console.log("update", update);
  }

  const handleEditClick = useCallback(async () => {
    if (!audioUrl || !audioUrl.url || !audioUrl.name) return;
    if (!midiUrl || !midiUrl.url || !midiUrl.name) return;

    await saveToDB(audioUrl, midiUrl);

    pageSetAudioUrl(audioUrl);
    pageSetMidiUrl(midiUrl);
    setShouldLoadFiles(true);
  }, [
    audioUrl,
    midiUrl,
    pageSetAudioUrl,
    pageSetMidiUrl,
    setShouldLoadFiles,
  ]);

  const handleClearClick = useCallback(() => {
    setAudioUrl(undefined);
    setMidiUrl(undefined);
    pageSetAudioUrl(undefined);
    pageSetMidiUrl(undefined);
  }, []);

  const editEnabled = useMemo(() => {
    return !!audioUrl && !!midiUrl;
  }, [audioUrl, midiUrl]);

  return (<>
    <Box component={"p"} sx={{ mt: 2 }}>
      <input
        type="file"
        accept="audio/*"
        onChange={handleAudioFileChange}
        style={{ display: "none" }}
        id="audio-file-input"
      />
      <label htmlFor="audio-file-input">
        <Button variant="contained" component="span">
          Upload Audio
        </Button>
      </label>
      <input
        type="file"
        accept=".mid"
        onChange={handleMidiFileChange}
        style={{ display: "none" }}
        id="midi-file-input"
      />
      <label htmlFor="midi-file-input">
        <Button variant="contained" component="span">
          Upload MIDI
        </Button>
      </label>
    </Box>
    {audioUrl && (
      <Typography variant="body1">Audio file: {audioUrl.name} ({audioUrl.url ? "uploaded" : "uploading..."})</Typography>
    )}
    {midiUrl && (
      <Typography variant="body1">MIDI file: {midiUrl.name} ({midiUrl.url ? "uploaded" : "uploading..."})</Typography>
    )}
    <ButtonGroup>
      <Button
        variant="contained"
        onClick={handleEditClick}
        disabled={!editEnabled}
      >
        Upload
      </Button>
      <Button
        variant="contained"
        onClick={handleClearClick}
        disabled={!editEnabled}
      >
        Clear
      </Button>
    </ButtonGroup>
  </>);
}

export default FileUploader;