import { List, ListItemIcon, ListItemButton, ListItem, ListItemText } from "@mui/material";
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import EditIcon from '@mui/icons-material/Edit';
import HeadphonesIcon from '@mui/icons-material/Headphones';
import CellTowerIcon from '@mui/icons-material/CellTower';
import BugReportIcon from '@mui/icons-material/BugReport';

export const Home = () => {

  const nav = [
    {
      path: "/play",
      text: "Play",
      icon: PlayCircleOutlineIcon,
    },
    {
      path: "/edit",
      text: "Edit",
      icon: EditIcon,
    },
    {
      path: "/edit?test",
      text: "Edit (test)",
      icon: BugReportIcon,
    },
    {
      path: "/listen",
      text: "Listen",
      icon: HeadphonesIcon,
    },
    {
      path: "/serve",
      text: "Serve",
      icon: CellTowerIcon,
    },
  ];
  return (<>
    <List>
      {nav.map((item, index) => {
        const { icon: Icon, text, path } = item;
        return <ListItem key={`nav-${index}`}>
          <ListItemButton
            onClick={() => {
              const url = `${process.env.PUBLIC_URL}${path}`;
              window.location.replace(url);
            }}
          >
            <ListItemIcon>
              <Icon />
            </ListItemIcon>
            <ListItemText primary={text} />
          </ListItemButton>
        </ListItem>
      })}
    </List>
  </>);
};